@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Rubik','Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  background: -webkit-gradient(linear, left bottom, left top, from(#e2d9d9), to(#ffffff));
  background: linear-gradient(to top, #e2d9d9, #ffffff);
  background-repeat: no-repeat;
}

code {
  font-family:'Rubik','Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
}



